import React, { LegacyRef, MutableRefObject } from 'react';
import { render } from 'react-dom';
import * as R from 'ramda';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { FONT_STROKE_COLORS, t_fontStrokeColor } from './const';

const FontStrokeColorSelector = React.memo(
	(props: {
		strokeColorNames: string[];
		onChange: (event: SelectChangeEvent) => any;
		value: string;
	}) => {
		console.log({
			strokeColorNames: props.strokeColorNames,
			value: props.value,
		});
		return (
			<FormControl fullWidth size='small'>
				<InputLabel id='font-stroke-color-label'>Font Stroke Color</InputLabel>
				<Select
					labelId='font-stroke-color-label'
					id='font-stroke-color'
					value={String(props.value)}
					label='Font Stroke Color'
					onChange={props.onChange}
				>
					{props.strokeColorNames.map((e, i) => {
						return (
							<MenuItem key={i} value={e}>
								{e}
							</MenuItem>
						);
					})}
				</Select>
			</FormControl>
		);
	},
);
export default FontStrokeColorSelector;
