import * as React from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Button from '@mui/material/Button';
import { TextField } from '@mui/material';
const BgImageSelectTabs = React.memo(
	(props: {
		localImageInputId: string;
		currentTabNumber: string;
		onChange: (event: React.SyntheticEvent, newValue: string) => any;
	}) => {
		return (
			<Box sx={{ width: '100%', typography: 'body1' }}>
				<TabContext value={props.currentTabNumber}>
					<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
						<TabList
							onChange={props.onChange}
							aria-label='lab API tabs example'
						>
							<Tab label='背景画像URL' value='1' />
							<Tab label='背景画像をアップロード' value='2' />
							<Tab label='背景色' value='3' />
						</TabList>
					</Box>
					<TabPanel value='1'>Item One</TabPanel>
					<TabPanel value='2'>
						<ImageSelector localImageInputId={props.localImageInputId} />
					</TabPanel>
					<TabPanel value='3'>Item Three</TabPanel>
				</TabContext>
			</Box>
		);
	},
);

const ImageSelector = (props: { localImageInputId: string }) => {
	const on = React.useCallback(
		(e) => document.getElementById(props.localImageInputId)?.click(),
		[props.localImageInputId],
	);
	return (
		<div
			style={{
				display: 'flex',
				flexDirection: 'row',
				justifyContent: 'flex-start',
				//alignItems: 'flex-end',
			}}
		>
			<TextField
				id='bgimage-input'
				label='Outlined'
				variant='outlined'
				size='small'
				fullWidth
			/>
			<Button variant='contained' onClick={on} style={{ marginLeft: 8 }}>
				<span style={{ whiteSpace: 'nowrap' }}>アップロード</span>
			</Button>
			<InvisibleInput localImageInputId={props.localImageInputId} />
		</div>
	);
};

const InvisibleInput = (props: { localImageInputId: string }) => (
	<input
		id={props.localImageInputId}
		type='file'
		accept='image/*'
		style={{ display: 'none' }}
	/>
);

export default BgImageSelectTabs;
